body {
  text-align: center;
  font-family: Helvetica, Arial, sans-serif;
  top: 0;
  left: 0;
  box-sizing: border-box;
}

h2 {
  font-size: 1.5em;
  font-weight: 600;
  margin: 52px 0 40px;
  color: #000000;
  width: 100%;
  padding: 10px 0;
  background-color: rgba(211, 208, 208, 0.1);
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a {
  text-decoration: none;
}

.main-nav ul {
  width: 100%;
  height: 12vh;
  padding-top: 0;
  margin-top: 0;
  margin-bottom: 50px;
  display: flex;
  justify-content: center;
}

.main-nav li {
  width: 100%;
  height: 1vh;
  max-width: 120px;
  margin-right: 10px;
  margin-top: 1em;
  margin-bottom: 2em;
}

.main-nav a {
  font-size: 1em;
  font-weight: 600;
  display: block;
  background: rgba(0, 0, 0, 0.4);
  outline: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 5px;
  color: #fff;
}

.main-nav a:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

@media only screen and (max-width: 480px) {
  .photo-container li {
    margin: auto;
  }
}

@media only screen and (min-width: 481px) {
  .container {
    max-width: 960px;
    margin: auto;
  }

  .main-nav ul {
    display: flex;
    justify-content: center;
  }
}

@media only screen and (max-width: 600px) {
  .photo-container li {
    margin: auto;
  }

  .main-nav {
    display: flex;
    justify-content: center;
  }

  .main-nav ul {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 2;
    width: 300px;
    height: 120px;
    justify-content: center;
  }
}

@media screen and (min-width: 800px) {
  .photo-container li {
    margin: inherit;
  }
}
